.book-block {
  display: flex;
  flex-wrap: wrap;
  border: 1px black solid;
  margin: 10px;
  padding: 5px;
  h3,
  h4,
  p {
    text-align: left;
  }
  h3,
  h4 {
    max-width: 200px;
  }
  h4 {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  a {
    text-decoration: none;
    color: inherit;
  }
}
.book-block:hover {
  background-color: #eee;
}
